.main-nav {
  position: relative;
  display: flex;
  width: 100%;
}

.main-nav__toggle {
  position: absolute;
  width: 21px;
  height: 14px;
  background-color: transparent;
  top: -48px;
  right: 31px;
  border: none;
  z-index: 6;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::after {
  position: absolute;
  content: "";
  background-image: url("../img/icon-menu-open.svg");
  width: 21px;
  height: 14px;
  background-repeat: no-repeat;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.main-nav--opened .main-nav__toggle::after {
  position: absolute;
  content: "";
  background-image: url("../img/icon-menu-close.svg");
  width: 21px;
  height: 21px;
  background-repeat: no-repeat;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.main-nav__wrapper {
  display: none;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  z-index: 5;
}
