@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  font-display: auto;
  src: local("Open Sans"), local("OpenSans"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans Bold";
  font-style: normal;
  font-weight: 700;
  font-display: auto;
  src: local("Open Sans Bold"), local("OpenSans-Bold"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  margin: 0 auto;
  padding: 0;
  font-family: "Open Sans", "Roboto", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: 800;
  color: black;
  background-color: #ffffff;
  background-position: top center;
  background-repeat: no-repeat;
  height: 100%;
}

.page-wrapper {
  position: relative;
  min-width: 320px;
  max-width: 1150px;
  height: 100%;
  background-color: #ffffff;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  text-align: center;
  color: #444444;
  vertical-align: middle;
  text-transform: uppercase;
  background-color: transparent;
  border: 2px solid #444444;
}
.button:hover {
  color: #ffffff;
  background-color: #444444;
  transition: all 0.5s;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
}

a {
  text-decoration: none;
}

.modal-show {
  display: flex;
}